import {
  Component, EventEmitter, Input,
  Output
} from '@angular/core';
import { Router, RouterModule } from '@angular/router';
import { FrontendLogger, getBizNestErrorResponseMessage, UserIp } from '@biz-nest/auth-client';
import {
  EventbusService, TooltipService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';

@Component({
  selector: 'app-view-user-ip',
  standalone: true,
  imports: [
    RouterModule
  ],
  templateUrl: './view-user-ip.component.html'
})
export class ViewUserIpComponent {
  @Input({ required: true }) userIp!: UserIp;
  @Input({ required: true }) showModal!: boolean;
  @Output() closeModal = new EventEmitter<void>();

  constructor(
    public eventbus: EventbusService,
    public tooltipService: TooltipService,
    public toastr: ToastrService,
    private router: Router
  ) {}

  async deleteUserIp() {
    try {
      await this.userIp.remove();
      this.closeModal.emit();
    } catch (error) {
      FrontendLogger.error(error);
      this.toastr.error(getBizNestErrorResponseMessage(error), 'Error');
    }
  }

  async blockUnblockUser() {
    try {
      await this.userIp.update({ blocked: {
        status: !this.userIp.blocked.status,
        loginAttemptRef: '',
        timesBlocked: !this.userIp.blocked.status ? 1 : 0
      } });
      this.closeModal.emit();
    } catch (error) {
      FrontendLogger.error(error);
      this.toastr.error(getBizNestErrorResponseMessage(error), 'Error');
    }
  }

  viewUser() {
    this.closeModal.emit();
    this.router.navigate(['/manage-user/' + this.userIp.user._id]);
  }
}

