import {
  Component, EventEmitter, OnInit,
  Output
} from '@angular/core';
import { Router, RouterModule } from '@angular/router';
import { UserIp } from '@biz-nest/auth-client';
import { FrontendLogger, IfilterProps } from '@biz-nest/core-client';
import {
  EventbusService, TooltipService
} from '@front-shared-lib';

@Component({
  selector: 'app-user-ips-cards',
  standalone: true,
  imports: [
    RouterModule
  ],
  templateUrl: './user-ips-cards.component.html'
})
export class UserIpsCardsComponent
implements OnInit {
  @Output() filterBy = new EventEmitter<IfilterProps>();
  total = 0;
  active = 0;
  blocked = 0;

  constructor(
    public eventbus: EventbusService,
    public tooltipService: TooltipService,
    private router: Router
  ) {}

  ngOnInit(): void {
    this.getTotal();
    this.getActive();
    this.getBlocked();
  }

  async getTotal() {
    try {
      const { count } = await UserIp.filterAll({
        returnEmptyArray: true
      });

      this.total = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getActive() {
    try {
      const { count } = await UserIp.filterAll({
        propFilter: {
        // eslint-disable-next-line @typescript-eslint/naming-convention
          'blocked.status': false
        },
        returnEmptyArray: true
      });

      this.total = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  async getBlocked() {
    try {
      const { count } = await UserIp.filterAll({
        propFilter: {
        // eslint-disable-next-line @typescript-eslint/naming-convention
          'blocked.status': false
        },
        returnEmptyArray: true
      });

      this.total = count;
    } catch (error) {
      FrontendLogger.error(error);
    }
  }

  goToAnalytics() {
    this.router.navigate(['/user-ips-analytics']);
  }
}

