import {
  Component, HostListener, OnDestroy, OnInit
} from '@angular/core';
import { Meta, Title } from '@angular/platform-browser';
import { Router, RouterModule } from '@angular/router';
import { UserIp } from '@biz-nest/auth-client';
import { expenseCategory, FrontendLogger, getBizNestErrorResponseMessage } from '@biz-nest/core-client';
import {
  AppNameService,
  EventbusService,
  ListComposeAdminPanel,
  MainBarTitleService,
  scrollToTop,
  TooltipService, WindowService
} from '@front-shared-lib';
import { ToastrService } from 'ngx-toastr';
import { AdminDateFilterFieldComponent } from '../../../misc/admin-date-filter-field/admin-date-filter-field.component';
import { AdminModalComponent } from '../../../misc/admin-modal/admin-modal.component';
import { AdminPageContentComponent } from '../../../misc/admin-page-content/admin-page-content.component';
import { AdminSearchFieldComponent } from '../../../misc/admin-search-field/admin-search-field.component';
import { AdminTableComponent } from '../../../misc/admin-table/admin-table.component';
import { AdminTopBarComponent } from '../../../misc/admin-top-bar/admin-top-bar.component';
import { UserIpsCardsComponent } from '../user-ips-cards/user-ips-cards.component';
import { ViewUserIpComponent } from '../view-user-ip/view-user-ip.component';

@Component({
  selector: 'app-user-ips-list',
  standalone: true,
  imports: [
    RouterModule,
    AdminModalComponent,
    UserIpsCardsComponent,
    AdminTableComponent,
    AdminSearchFieldComponent,
    AdminDateFilterFieldComponent,
    AdminTopBarComponent,
    AdminPageContentComponent,
    ViewUserIpComponent
  ],
  templateUrl: './user-ips-list.component.html'
})
export class UserIpsListComponent
implements OnInit, OnDestroy {
  expenseCategory = expenseCategory;
  cleanup?: () => void;
  catFilter?: string;

  listCompose = new ListComposeAdminPanel<UserIp>(
    UserIp,
    this.eventbus,
    this.windowService,
    this.toastr
  );

  userIp!: UserIp;

  actions = [
    {
      label: 'Update',
      icon: 'edit',
      event: (userIp: UserIp) => {
        this.viewUser(userIp._id);
      }
    },
    {
      label: 'View',
      icon: 'visibility',
      event: (userIp: UserIp) => {
        this.showViewModal = true;
        this.userIp = userIp;
      }
    },
    {
      label: 'View User',
      icon: 'visibility',
      event: (userIp: UserIp) => {
        this.viewUser(userIp._id);
      }
    },
    {
      label: 'blockUnBlock',
      icon: 'visibility',
      event: (userIp: UserIp) => {
        this.blockUnblockUser(userIp, userIp.blocked.status);
      }
    },
    {
      label: 'Delete',
      icon: 'delete',
      event: (userIp: UserIp) => {
        this.listCompose.openDeleteOneModal(
          userIp._id,
          'Delete User Ip',
          'Are You Sure You Want To Delete This User Ip Record?'
        );
      }
    }
  ];

  showViewModal = false;

  constructor(
    public eventbus: EventbusService,
    private router: Router,
    private windowService: WindowService,
    private toastr: ToastrService,
    public tooltipService: TooltipService,
    private apNme: AppNameService,
    public mainBarTitle: MainBarTitleService,
    private metaTagService: Meta,
    private titleService: Title
  ) {}

  @HostListener('document:click', ['$event'])
  onDocumentClick() {
    this.listCompose.currentActivePopupId = '';
  }

  ngOnInit(): void {
    this.listCompose.getDataArr();
    this.mainBarTitle.title = 'User Ips - Manage User Ips Records';
    this.titleService.setTitle(this.mainBarTitle.title + ' - ' + this.apNme.name);
    this.metaTagService.updateTag({
      name: 'description',
      content: `Easily manage and view user IP addresses for your organization
      to ensure effective tracking and security.`
    });

    scrollToTop(this.windowService);
  }


  viewUser(id: string) {
    this.router.navigate(['/manage-user/' + id]);
  }

  resolveBlocked(blocked) {
    if (!blocked) {
      return 'clean';
    }

    if (blocked.status) {
      return 'blocked about ' + blocked.timesBlocked;
    }

    return 'clean';
  }

  async blockUnblockUser(userIp: UserIp, block: boolean) {
    try {
      const blocked = {
        status: block,
        loginAttemptRef: '',
        timesBlocked: block ? 1 : 0
      };

      await userIp.update({ blocked });
    } catch (error) {
      FrontendLogger.error(error);
      this.toastr.error(getBizNestErrorResponseMessage(error), 'Error');
    }
  }


  ngOnDestroy() {
    this.cleanup?.();
    this.listCompose.cleanup?.();
  }
}

